import React, { Component } from 'react';
import Posts from './Posts';

export default class App extends Component {
  
  goToSingle = (id) =>{
    // this.props.history.push({pathname:`/singlepost/${id}` , state: this.state.posts})
    this.props.history.push({pathname:`/singlepost/${id}`})

  }

  render() {
    return (
      <div>
        <Posts posts={this.props.posts} goToSingle={this.goToSingle}/>
      </div>
    )
  }
}
