import React, { Component } from 'react'
import {Link} from 'react-router-dom';


export default class Single extends Component {
    Home= React.createRef();

    addComment = (e) =>{
        e.preventDefault();
        const name = e.currentTarget.children[0].value;
        const comment = e.currentTarget.children[1].value;
        const id = this.props.match.params.postId;
        this.props.addComment(name , comment , id);
        e.currentTarget.reset();
        e.currentTarget.children[0].focus();
        
        
    }

    render() {
        // const id = this.props.match.params.postId;
        const id = this.props.match.params.postId;
        const posts = this.props.posts;
        const thisUser = posts[id]
        return (
            <div className='single'>
                <div className='post'>
                    <h2 id='test' ref={this.Home}><Link to='/'>Home</Link></h2>
                    <h2 className='post-title'>{thisUser.title}</h2>
                    <figure>
                        <img src={`../${thisUser.image}`} alt={thisUser.name}/>
                    </figure>
                    <div className='post-info'>
                        <button className='like' >
                            <i className='fa fa-heart-o'></i>
                            <span>{thisUser.like}</span>
                        </button>
                        <button className='comment'>
                            <i className='fa fa-comment-o'></i>
                            <span>{thisUser.comments.length}</span> 
                        </button>
                    </div>
                    <div className='caption'>{thisUser.caption}</div>
                </div>
                <div className='post-comment'>
                    <ul className='comments'>
                        <h2>comments</h2>
                        {thisUser.comments.map((comment,index)=><li key={index}>
                            <strong>{comment.name}</strong>:<span>{comment.comment}</span>
                        </li>)}
                    </ul>
                </div>
                <form onSubmit={this.addComment}>
                    <input type='text' placeHolder='name'></input>
                    <input type='text' placeHolder='comment'></input>
                    <button className='btn-add'>Add</button>
                </form>
            </div>
        )
    }
}
