import React, { Component } from 'react';
import {BrowserRouter , Switch , Route} from 'react-router-dom'
import App from './App';
import Single from './Single';
import data from './data'



export default class Router extends Component {

    state={
        posts : data
    }

    addComment= (name,comment,index) =>{
        const posts = [...this.state.posts]
        posts[index].comments.push({name,comment})
        this.setState({posts})
    }


    

    render() {
        return (
            <BrowserRouter>
                <Switch>
                    <Route exact path= '/' render={(props) => <App {...props} posts={this.state.posts}/> }/>
                    <Route exact path= '/singlepost/:postId' render={(props) => <Single {...props} posts={this.state.posts} addComment={this.addComment}/> }/>
                </Switch>
                
            </BrowserRouter>
        )
    }
}
