import React, { Component } from 'react'

export default class Posts extends Component {

    userLike = (e)=>{
        e.currentTarget.children[0].classList.toggle('fa-heart-o');
        e.currentTarget.children[0].classList.toggle('fa-heart');
        if (e.currentTarget.children[0].classList.contains('fa-heart')) {
            e.currentTarget.children[1].innerHTML++;
        }else{
            e.currentTarget.children[1].innerHTML--;

        }
    }
    


    render() {
        return (
            <div className='post-wrapper'>
                <ul>
                    {this.props.posts.map((post , index)=> <li className='post' key={index}>    
                                <h2 className='post-title'>{post.title}</h2>
                                <figure>
                                    <img src={post.image} alt={post.name}/>
                                </figure>
                                <div className='post-info'>
                                    <button className='like' onClick={this.userLike}>
                                        <i className='fa fa-heart-o'></i>
                                        <span>{post.like}</span>
                                    </button>
                                    <button onClick={()=>this.props.goToSingle(index)} className='comment'>
                                        <i className='fa fa-comment-o'></i>
                                        <span>{post.comments.length}</span> 
                                    </button>
                                </div>
                                <div className='caption'>{post.caption}</div>
                            </li>
                        )
                    }
                </ul>
            </div>
        )
    }
}
